  border-bottom-right-radius: calc(0.75rem - 1px);
  border-bottom-left-radius: calc(0.75rem - 1px);
}
.modal-header {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1rem;
  border-bottom: 1px solid #2b2a2a;
  border-top-left-radius: calc(0.75rem - 1px);
  border-top-right-radius: calc(0.75rem - 1px);
}

.avatar-content {
  width: 150px;
}
</style>
<script>
import moment from 'moment'
import ModalPanel from './ModalPanel.vue'
export default {
  name: 'order-list',
  components: {
    ModalPanel,
  },
  props: {
    data: {
      type: Array,
      required: true,
    },
    restaurantCount: {
      type: Number,
    },
  },
  data() {
    return {
      url: 'https://www.google.com/maps/dir',
      title: 'Modal title',
      selectedOrder: {},
      provider: {},
      client: {},
      deliveryAddress: {},
      products: [],
      reasons: [],
      open: false,
    }
  },
  computed: {},
  created() {
    // Get the darkMode flag from session storage
    const isDarkModeOn = window.localStorage.getItem('darkMode') === 'true'

    // Set the isDarkMode data property to the value from session storage
    this.$store.state.darkMode = isDarkModeOn
  },
  methods: {
    showModal(order) {
      this.selectedOrder = order
      this.provider = order.provider
      this.products = order.products
      this.client = order.client
      this.deliveryAddress = this.client.deliveryAddress

      this.open = true
    },
    again(id) {
      fetch('/ajax/sendAgain?id=' + id, {
        method: 'GET',
      }).then(async (response) => {
        let result = await response.json()
        console.log(result)
        this.open = false
      })
    },
    saveReason(reason, id) {
      console.log(id)
      fetch('/ajax/saveReason?reason=' + reason + '&id=' + id, {
        method: 'GET',
      }).then(async (response) => {
        let result = await response.json()
        console.log(result)
        this.open = false
      })
    },
    cancel(id) {
      fetch('/ajax/getReasons?id=' + id, {
        method: 'GET',
      }).then(async (response) => {
        let result = await response.json()
        this.reasons = result.data
      })
    },
    onayla(id) {
      fetch('/ajax/onayla?id=' + id, {
        method: 'GET',
      }).then(async (response) => {
        let result = await response.json()
        console.log(result)
        this.open = false
      })
    },

    getStatus(status) {
      let statusText = 'Onayland'
      let color = 'bg-success'
      if (status == 400) {
        statusText = 'Onay Bekliyor'
        color = 'bg-danger'
      }

      if (status == 550) {
        statusText = 'Sipari Hazrland'
        color = 'bg-success'
      }
      if (status == 600) {
        statusText = 'Sipari Kuryeye Teslim Edildi'
        color = 'bg-success'
      }
      if (status == 700) {
        statusText = 'Kurye Yola kt'
        color = 'bg-info'
      }
      if (status == 800) {
        statusText = 'Kurye Adrese Ulat'
        color = 'bg-success'
      }
      if (status == 900) {
        statusText = 'Sipari Teslim Edildi.'
        color = 'bg-success'
      }
      if (status == 1600) {
        statusText = 'Restoran ptal Etti'
        color = 'bg-danger'
      }
      if (status == 1900) {
        statusText = 'Platform ptal Etti'
        color = 'bg-danger'
      }

      return `<span class="badge badge-lg w-100 ${color}" style="text-transform: none">${statusText}</span>`
    },
    imageURL(slug) {
      slug = this.$store.state.darkMode ? slug + '-dark' : slug
      return '../../' + slug + '.png'
    },
    formatDate(date) {
      return moment(date).format('HH:mm')
    },
    formatDateWithDay(date) {
      return moment(date).format('DD.MM.YYYY HH:mm')
    },
    getCode(order) {
      return order.provider.slug == 'getir'
        ? order.confirmationId
        : order.shortCode
    },
  },
}
</script>
