<template>
  <div class="d-flex align-items-center justify-content-center vh-100">
    <div class="col-md-6">
      <form @submit.prevent="upload" enctype="multipart/form-data">
        <div class="card">
          <div class="card-header">
            <h5 class="card-title">Bossentegra</h5>
          </div>
          <div class="card-body">
            <div class="col-md-12">
              <label for="validationCustom01" class="form-label">
                Dosya Giriniz
              </label>
              <input
                type="file"
                @change="handleFileUpload($event)"
                class="form-control"
              />
            </div>
          </div>
          <div class="card-footer">
            <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
            <button class="btn btn-primary w-100">{{ buttonText }}</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<style scoped>
.error {
  color: red;
}
</style>

<script>
import axios from 'axios'
export default {
  name: 'UploadPanel',
  data() {
    return {
      file: '',
      buttonText: 'Gnder',
      errorMessage: false,
    }
  },
  methods: {
    handleFileUpload(event) {
      this.file = event.target.files[0]
    },
    upload() {
      let formData = new FormData()
      formData.append('file', this.file)
      // code to handle login request
      this.buttonText = 'Gnderiliyor'
      axios
        .post('https://app.posentegra.com/api/file-upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Authorization: 'Bearer ' + this.$store.state.token,
          },
        })
        .then(function () {
          this.file = null
          this.formMessage = 'Ayarlarnz Kaydedildi'
        })
        .catch(function () {
          this.error = true
          this.errorMessage = 'Ltfen dosyay Kontrol Edin.'
        })
    },
  },
}
</script>
